
import MD5 from 'spark-md5';
import Utils from "./Utils.js";
import * as Xhr from "./XmlHttpRequest.js";
import Dialog from './Dialog.js';

export default class OtherWap{
    /**SDK*/
    static initQHSdk(){
        const loadSdkJS = (src) =>{
            return new Promise((resolve,reject) =>{
                let oHead = document.getElementsByTagName('head').item(0);
                let oScript= document.createElement("script");
                oScript.type = "text/javascript";
                oScript.src = src;
                oHead.appendChild(oScript);
                oScript.onload = function(){
                    return resolve("QHSKD INIT SUCCESS");
                };
            })
        }
        return loadSdkJS("//port.2r3r.com/game/qhjssdk").then((msg)=>{
            const initdata = {
                "username":flashvars.qh_username,	//idusername
                "gid":flashvars.qh_qhgid,	//id
                "qhchannel":flashvars.qh_qhchannel,	//qhchannel
                "qhchannelid":flashvars.qh_qhchannelid	//idqhchannelid
            }
            window.qhsdk.init(initdata);
            return Promise.resolve(msg)
        });
    };

    /**
     * 
     * @param price         
     * @param goodsId       id
     * @param indentId      ID
     * @param chapteruuid   uuid
     * @param context       context
     */
    static pay(price, goodsId, indentId,chapteruuid,context,payindex,callback){
        const sign = MD5.hash(price.toString() + flashvars.qh_username + indentId + flashvars.qh_key);
        const paydata = {
            "userId":flashvars.qh_username,
            "gid":flashvars.qh_qhgid,
            'goodsId':goodsId,
            "goodsName":price + "",
            "money":price,
            "roleName":"3000",
            "ext":indentId,
            "serverId":flashvars.qh_serverid,
            "sign":sign
        };
        if(payindex ===1){context.payValues[0].name = encodeURI(context.payValues[0].name)};
        const sydata = {
            "game_id":flashvars.gid
            ,"uuid":flashvars.uuid
            ,"chapter_uuid":chapteruuid
            ,"amount":price
            ,"set_value":payindex === 0 ? "":Utils.encodeBase64(JSON.stringify(context.payValues))
            ,"payindex":payindex
            ,"ext":indentId
            ,"event_id":goodsId
            ,"qh_username":flashvars.qh_username
            ,"qh_qhgid":flashvars.qh_qhgid
        }
        const send = (url,d)=>{
            Xhr.xhrSend(url, d, null, 4000).then(rs => {
                //send success
            }, rj => {
                //send fail
            });
        };
        send("https://pay.3000.com/qunhei/paydata.php",JSON.stringify(sydata));
        //
        window.qhsdk.pay(paydata,()=>{
            console.log("")
        });
        Utils.timeout(200).then(()=>{
            //
            if(payindex === 0){
                Dialog.custom("Info","","","","",false).then(()=>{
                    if(callback) callback("success");
                },()=>{
                    if(callback) callback("cancel");
                })
            }else{
                Dialog.custom("Info","","","","",false).then(()=>{
                    window.location.reload()
                },()=>{
                    if(callback) callback();
                })
            }

        })
    };
    /***/
    static sendQHData(){
        const send = (url,d)=>{
            return Xhr.xhrSend(url, d, null, 4000).then(rs => {
                //send success
                if (rs.responseText.length === 0) {
                    //Get failed, no response data
                    return Promise.reject('Get failed, no response data');
                }
                let retData = JSON.parse(rs.responseText);
                if(retData.status === 0 && retData.success === true && retData.data){
                    flashvars.uid = retData.data.uid;
                    flashvars.token = retData.data.token;
                    flashvars.u = retData.data.u;
                    return Promise.resolve();
                }
            }, rj => {
                //send fail
            });
        };
        const qhdata = {
            "qh_username":flashvars.qh_username
            ,"qh_serverid":flashvars.qh_serverid
            ,"qh_time":flashvars.qh_time
            ,"qh_isadult":flashvars.qh_isadult
            ,"qh_flag":flashvars.qh_flag
            ,"qh_uid":flashvars.qh_uid
            ,"qh_nname":Utils.encodeBase64(flashvars.qh_nname)
            ,"qh_qhchannel":flashvars.qh_qhchannel
            ,"qh_qhchannelid":flashvars.qh_qhchannelid
            ,"refresh_token":1
        }
        return send("https://ptlogin.3000.com/?m=ptlogin&ac=qhlogin",JSON.stringify(qhdata));
    }
}